@import url(https://fonts.googleapis.com/css?family=Satisfy);
@import url(https://fonts.googleapis.com/css?family=Acme);
@import url(https://fonts.googleapis.com/css?family=Droid+Serif:400,400italic|Montserrat:400,700);
@import url(https://fonts.googleapis.com/css?family=Satisfy);
@import url(https://fonts.googleapis.com/css?family=Acme);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  	background-image: linear-gradient(to top, #c1dfc4 0%, #deecdd 100%) cover;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.box-wrapper {
 	margin : 1em;
 	display: inline-block;
	 background-color: #fff;
	 overflow: hidden;
	 max-width: 320px;
	 box-shadow: 0px 5px 43px rgba(0, 0, 0, 0.48);
	 padding: 0;
	 text-align: center;
}
 .box-wrapper img {
	 max-width: 100%;
}
 .title {
	 font-size: 2em;
	 font-weight: 900;
	 text-transform: uppercase;
	 margin: 0;
	 font-family: 'Acme', sans-serif;
}
 .desc {
	 font-family: 'Satisfy', cursive;
	 font-size: 1.2em;
}
 .box-content {
	 position: relative;
	 z-index: 1;
}
 .box-content:before {
	 content: '';
	 width: 200%;
	 height: 100px;
	 position: absolute;
	 display: block;
	 background-color: #fff;
	 transform: rotate(-8deg);
	 top: -50px;
	 left: -10%;
	 z-index: -1;
}
 .box-content .buy {
	 display: block;
	 top: -80px;
	 right: 30px;
	 z-index: 2;
	 width: 70px;
	 height: 70px;
	 position: absolute;
	 transition: all 1s ease;
}
.box-content .buy:hover{
	cursor: pointer;
}
 .box-content .buy span {
	 width: 70px;
	 height: 70px;
	 background-color: #06d6a0;
	 display: block;
	 border-radius: 50%;
	 position: absolute;
	 font-size: 36px;
	 color: #fff;
	 transform: translate(-50%, -50%);
	 top: 50%;
	 left: 50%;
	 
}
 .box-content .buy span i {
	 padding: 15px;
	 transition: all 0.3s ease-out;
}
 .box-content .price {
	 font-size: 2em;
	 font-weight: 900;
	 display: block;
	 width: 100px;
	 margin: 20px auto 0;
	 position: relative;
	 font-family: 'Satisfy', cursive;
}
 .box-content .price:after, .box-content .price:before {
	 content: '';
	 height: 1px;
	 width: 50px;
	 display: block;
	 position: absolute;
	 background-color: #ddd;
	 top: 18px;
}
 .box-content .price:before {
	 left: -50px;
}
 .box-content .price:after {
	 right: -50px;
}
.fa-plus{
	pointer-events: none;
}

 * {
	 box-sizing: border-box;
}
 body {
	 color: #333;
	 -webkit-font-smoothing: antialiased;
	 font-family: 'Droid Serif', serif;
}
 img {
	 max-width: 100%;
}
 .cf:before, .cf:after {
	 content: " ";
	 display: table;
}
 .cf:after {
	 clear: both;
}
.line{
	border: 1px dashed #bcbcbc;
	}
 .wrap {
	 width: 75%;
	 max-width: 960px;
	 margin: 0 auto;
	 padding: 2% 0;
	 margin-bottom: 3em;
}
 .heading {
	 padding: 1em 0;
	 border-bottom: 1px solid #d0d0d0;
}
 .heading h1 {
	 font-family: 'Droid Serif', serif;
	 font-size: 2em;
	 float: left;
}
.continue:link,.continue:visited {
	 text-decoration: none;
	 font-family: 'Montserrat', sans-serif;
	 letter-spacing: -0.015em;
	 font-size: 0.75em;
	 padding: 1em;
	 color: #fff;
	 background: #82ca9c;
	 font-weight: bold;
	 border-radius: 50px;
	 float: right;
	 text-align: right;
	 transition: all 0.25s linear;
}
.continue:after {
	 content: "\276f";
	 padding: 0.5em;
	 position: relative;
	 right: 0;
	 transition: all 0.15s linear;
}
.continue:hover {
	 background: #f69679;
}
.continue:hover:after{
	 right: -10px;
}
 .cart {
	 padding: 1em 0;
}
 .cart .items {
	 display: block;
	 width: 100%;
	 vertical-align: middle;
	 padding: 1.5em;
	 border-bottom: 1px solid #fafafa;
	  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}
 .cart .items .infoWrap {
	 display: table;
	 width: 100%;
}
 .cart .items .cartSection {
	 display: table-cell;
	 vertical-align: middle;
}
 .cart .items .cartSection .itemNumber {
	 font-size: 0.75em;
	 color: #777;
	 margin-bottom: 0.5em;
}
 .cart .items .cartSection h3 {
	 font-size: 1em;
	 font-family: 'Montserrat', sans-serif;
	 font-weight: bold;
	 text-transform: uppercase;
	 letter-spacing: 0.025em;
}
 .cart .items .cartSection p {
	 display: inline-block;
	 font-size: 0.85em;
	 color: #777;
	 font-family: 'Montserrat', sans-serif;
}
 .cart .items .cartSection p .quantity {
	 font-weight: bold;
	 color: #333;
}
 .cart .items .cartSection .itemImg {
	 width: 4em;
	 float: left;
}
 .cart .items .cartSection.qtyWrap, .cart .items .cartSection.prodTotal {
	 text-align: center;
}
 .cart .items .cartSection.qtyWrap p, .cart .items .cartSection.prodTotal p {
	 font-weight: bold;
	 font-size: 1.25em;
	 margin: 1.2em;
}
 .cart .items .cartSection input.qty {
	 width: 2em;
	 text-align: center;
	 font-size: 1em;
	 padding: 0.25em;
	 margin: 1em 0.5em 0 0;
}
 .cart .items .cartSection p.add-qty {
	font-size: 1em;	
	color: #465ff2;
}
 .cart .items .cartSection .add-qty i{
 	border-radius: 50%;
 	border: 1px solid black;
 	color: grey;
 	padding: .2em;
 	margin: .3em;
 	cursor: pointer;
 }
  .cart .items .cartSection .add-qty i:hover{
 	color: green;
 }
 .cart .items .cartSection .itemImg {
	 width: 12em;
	 display: inline;
	 padding-right: 1em;
}
 .glyphicon-trash:hover {
	 color: red;
	 cursor: pointer;
}
 .promoCode {
	 border: 2px solid #efefef;
	 float: left;
	 width: 35%;
	 padding: 2%;
}
 .promoCode label {
	 display: block;
	 width: 100%;
	 font-style: italic;
	 font-size: 1.15em;
	 margin-bottom: 0.5em;
	 letter-spacing: -0.025em;
}
 .promoCode input {
	 width: 85%;
	 font-size: 1em;
	 padding: 0.5em;
	 float: left;
	 border: 1px solid #dadada;
}
 .promoCode input:active, .promoCode input:focus {
	 outline: 0;
}
 .promoCode a.btn {
	 float: left;
	 width: 15%;
	 padding: 0.75em 0;
	 border-radius: 0 1em 1em 0;
	 text-align: center;
	 border: 1px solid #82ca9c;
}
 .promoCode a.btn:hover {
	 border: 1px solid #f69679;
	 background: #f69679;
}
 .btn:link, .btn:visited {
	 text-decoration: none;
	 font-family: 'Montserrat', sans-serif;
	 letter-spacing: -0.015em;
	 font-size: 1em;
	 padding: 1em 3em;
	 color: #fff;
	 background: #82ca9c;
	 font-weight: bold;
	 border-radius: 50px;
	 float: right;
	 text-align: right;
	 transition: all 0.25s linear;
}
 .btn:after {
	 content: "\276f";
	 padding: 0.5em;
	 position: relative;
	 right: 0;
	 transition: all 0.15s linear;
}
 .btn:hover{
	 background: #f69679;
}
 .btn:hover:after{
	 right: -10px;
}
 .promoCode .btn {
	 font-size: 0.85em;
	 paddding: 0.5em 2em;
}
/* TOTAL AND CHECKOUT */
 .subtotal {
	 float: right;
	 width: 35%;
}
 .subtotal .totalRow {
	 padding: 0.5em;
	 text-align: right;
}
 .subtotal .totalRow.final {
	 font-size: 1.25em;
	 font-weight: bold;
}
 .subtotal .totalRow span {
	 display: inline-block;
	 padding: 0 0 0 1em;
	 text-align: right;
}
 .subtotal .totalRow .label {
	 font-family: 'Montserrat', sans-serif;
	 font-size: 0.85em;
	 text-transform: uppercase;
	 color: #777;
}
 .subtotal .totalRow .value {
	 letter-spacing: -0.025em;
	 width: 35%;
}
ul{
	list-style-type:none;
}
.Empty{
	font-size: 2em;
	background: none;
}
 @media only screen and (max-width: 39.375em) {
	 .wrap {
		 width: 98%;
		 padding: 2% 0;
	}
	 .projTitle {
		 font-size: 1.5em;
		 padding: 10% 5%;
	}
	 .heading {
		 padding: 1em;
		 font-size: 90%;
	}
	 .cart .items .cartSection .itemImg {
		 width: 30%;
	}
	 .promoCode, .subtotal {
		 width: 100%;
	}
	 a.btn.continue {
		 width: 100%;
		 text-align: center;
	}
}
.prodTotal p{
	color: red }


.cart{
  margin-right:.3em;
}
